.mailpopup{
  background: rgba(10, 10, 10, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 80;
  opacity: 0;
  visibility: hidden;
  transition: var(--transition);
}
.mailpopup__wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.mailpopup__wrapper h2{
  max-width: 800px;
  background-color: var(--color-bg);
  z-index: 90;
  border-radius: 2rem;
  color: var(--color-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5rem 8rem;
}
.mailpopup__wrapper h2 svg{
  margin-right: 10px;
}

@media screen and (max-width: 1024px) {
  .mailpopup__wrapper h2{
    padding: 4rem 7rem;
    font-size: 1rem;
  }
}
@media screen and (max-width: 600px) {
  .mailpopup__wrapper h2{
    padding: 3rem;
  }
}
@media screen and (max-width: 400px) {
  .mailpopup__wrapper h2{
    padding: 3rem 2rem;
  }
}
@media screen and (max-width: 370px) {
  .mailpopup__wrapper h2{
    padding: 3rem 1rem;
  }
}